// Highlight  
interface HighlightProps {
  color: 'primary' | 'secondary' | 'tertiary';
  inverted?: boolean;
  children: React.ReactNode;
}

const Highlight: React.FC<HighlightProps> = ({ color, inverted = false, children }) => {
  const getColor = () => {
    //    (   )
    if (inverted) {
      switch (color) {
        case 'primary':
          return COLORS.WHITE;
        case 'secondary':
          return '#A5B0FF'; //   
        case 'tertiary':
          return '#8FD9A3'; //   
        default:
          return COLORS.WHITE;
      }
    }
    //   (   )
    else {
      switch (color) {
        case 'primary':
          return COLORS.RED;
        case 'secondary':
          return COLORS.BLUE;
        case 'tertiary':
          return COLORS.GREEN;
        default:
          return COLORS.RED;
      }
    }
  };
  
  return <HighlightSpan color={getColor()}>{children}</HighlightSpan>;
};

// HighlightSpan   
const HighlightSpan = styled.span`
  color: ${props => props.color};
  position: relative;
  font-weight: 700;
`;

// MarketBar  props 
interface MarketBarProps {
  height: string;
  label: string;
  type: 'primary' | 'secondary' | 'tertiary';
  children: React.ReactNode;
}

const MarketBar: React.FC<MarketBarProps> = ({ height, label, type, children }) => {
  const getColor = () => {
    switch (type) {
      case 'primary':
        return COLORS.RED;
      case 'secondary':
        return COLORS.BLUE;
      case 'tertiary':
        return COLORS.GREEN;
      default:
        return COLORS.RED;
    }
  };
  
  return (
    <MarketBarWrapper>
      <BarValue>{label}</BarValue>
      <BarColumn color={getColor()} height={height}>
        <BarLabel>{children}</BarLabel>
      </BarColumn>
    </MarketBarWrapper>
  );
};

